#navBar {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 22px 0px;
  background-color: rgb(214, 231, 223);
}

#danaKenney {
  font-size: 20px;
  color: #538d72;
  margin: 0px;
  font-weight: 500;
  margin: 0px 0px 0px 33px;
}

.navMiddleItem {
  margin: 0px 10px;
  font-size: 13px;
  font-weight: 500;
}

#contactButton {
  border: none;
  outline: none;
  background-color: #538d72;
  color: white;
  font-size: 10px;
  padding: 10px 18px;
  cursor: pointer;
  margin: 0px 33px 0px 0px;
}

#contactButton:hover {
  background-color: #7fa895;
}
